import { Component, OnInit } from '@angular/core';
import {NewItemService} from './new-Item-service';

@Component({
  selector: 'app-ne-item',
  templateUrl: './ne-item.component.html',
  styleUrls: ['./ne-item.component.css'],
  providers: [NewItemService]
})
export class NeItemComponent implements OnInit {

  item = {
    isbn: '',
    title: '',
    auther: '',
    category: '',
    avalilableLanguage: '',
    subtitle: '',
    producer: ''

  };
  isError: Boolean = false;
  isSuccess: Boolean = false;
  isBook: Boolean =false;
  isCd: Boolean =false;

  selectOption(value) {
    this.item.category = value;
    console.log(this.item.category);
  }

  constructor(private newItemService: NewItemService) { }

  ngOnInit() {
  }
  submitNewItem() {
    console.log(this.item);
    this.isSuccess = true;
    this.newItemService.addNewItem(this.item)
     .then(response => {
      if (response.error) {
        this.isError = true;
     }
     if (response.success) {
         this.isSuccess = true;
     }
     })
     .catch(error => {
       throw error;
     });
  }
  event(event){
    if(this.item.category === '1'){
      console.log('1');
         this.isBook =true;
         this.isCd =false;

    } else if(this.item.category === '2'){
      this.isBook =false;
      this.isCd =true;
    }

  }

}
